  const handleOnChange = (e: any) => {

    var weekday=new Array(7);
    weekday[0]="monday";
    weekday[1]="tuesday";
    weekday[2]="wednesday";
    weekday[3]="thursday";
    weekday[4]="friday";
    weekday[5]="saturday";
    weekday[6]="sunday";
    setArray([]);
    setDataCsv([]);
    if (!e || !e.target || !e.target.files[0]) return
    setFile(e.target.files[0]);
    Papa.parse(e.target.files[0], {
      header: true,
      skipEmptyLines: true,
      complete: function (results) {
        if (!results.data || (results && results.data && results.data.length == 0)) {
          message.error('This file is empty')
          return
        }

        var format = /\./g
        let arr = new Array()
        results &&
          results.data.map((e: any) => {
         
           if (e.RANKING=="daily" && valueType!=2)
            {
              localStorage.setItem("dateCsv",e.RANKING)
            }
            if (e.RANKING!="daily" && valueType!=2 && valueType==6 && weekday.includes(e.RANKING.toLowerCase()))
            {
              localStorage.setItem("day",e.RANKING.toLowerCase())
            }
            if (e && e.RANKING && (e.RANKING.match(format)|| (e.RANKING=="daily" && valueType==2))) {
              localStorage.setItem('dateCsv', e.RANKING=="daily"?dateTodayes:e.RANKING.toLowerCase());
              setRanking(e.RANKING=="daily"?dateTodayes:e.RANKING.toLowerCase());
              return
            } else {
              if (!e.AGGCODE || e.AGGCODE == '') return
              if(valueType==6)
              {
                arr.push({
                  AGGCODE: e.AGGCODE,
                  RANKING: e.RANKING,
                  // DESCRIPTION: e.DESCRIPTION,
                  // PACK: e.PACK,
                  PRODCODE: e.PRODCODE,
                  DAY: localStorage.getItem('day'),
                })
              }
              else{
                arr.push({
                  AGGCODE: e.AGGCODE,
                  RANKING: e.RANKING,
                  // DESCRIPTION: e.DESCRIPTION,
                  // PACK: e.PACK,
                  PRODCODE: e.PRODCODE,
                  DATE: moment(localStorage.getItem('dateCsv'), 'DD.MM.YY').format('YYYY-MM-DD'),
                })
              }
             
            }
          })

        setDataCsv(arr);
      },
    })
  }

