import React ,{ useContext, useState, useEffect, useRef }from "react";
import {Space,Table,Form,Input, message,Button,Tooltip,Modal,Select,Divider,Tag} from "antd"
import type { FormInstance } from 'antd/es/form';
import {mainConfig} from '../../../config/mainConfig' 
import Helper from '../../utils/Helper'
import moment from "moment";
import { Sorter } from "../../utils/sorter";
import {PlusOutlined,EyeOutlined} from '@ant-design/icons'




const EditableContext = React.createContext<FormInstance<any> | null>(null);

let token = '',
  usrRole = ''
interface EditableRowProps {
  index: number;
}

const EditableRow: React.FC<EditableRowProps> = ({ index, ...props }) => {
  const [form] = Form.useForm();
  return (
    <Form form={form} component={false}>
      <EditableContext.Provider value={form}>
        <tr {...props} />
      </EditableContext.Provider>
    </Form>
  );
};
interface Item {
  key: string;
  name: string;
  age: string;
  address: string;
}
interface EditableCellProps {
  title: React.ReactNode;
  editable: boolean;
  children: React.ReactNode;
  dataIndex: keyof Item;
  record: Item;
  handleSave: (record: Item) => void;
}

const EditableCell: React.FC<EditableCellProps> = ({
  title,
  editable,
  children,
  dataIndex,
  record,
  handleSave,
  ...restProps
}) => {
  const [editing, setEditing] = useState<any>(false);
  const inputRef = useRef<any>(null);

  const form = useContext<any>(EditableContext);
  useEffect(() => {
    if (editing) {
      inputRef.current.focus();
    }
    
  }, [editing]);

  const toggleEdit = () => {
    setEditing(!editing);
    form.setFieldsValue({
      [dataIndex]: record[dataIndex]
    });
  };

  const save = async () => {
    try {
      const values = await form.validateFields();
      toggleEdit();
      handleSave({ ...record, ...values });
    } catch (errInfo) {
      console.log('Save failed:', errInfo);
    }
  };

  let childNode = children;

  if (editable) {
    childNode = editing ? (
      <Form.Item
        style={{
          margin: 0,
        }}
        name={dataIndex}
        rules={[
          {

            required: true,
            message: `${title} is required.`,
          },
          ({ getFieldValue }) => ({
            validator(rule, value) {
            
              if (value <=0) {
               
                return Promise.reject("Price should be greater than 0"); // The validator should always return a promise on both success and error
              } else {
                return Promise.resolve();
              }
            }
          })
        ]}
      >
        <Input ref={inputRef} onPressEnter={save} onBlur={save} />
      </Form.Item>
    ) : (
      <div
        className="editable-cell-value-wrap"
        style={{
          paddingRight: 24,
        }}
        onClick={toggleEdit}
      >
        {children}
      </div>
    );
  }

  return <td {...restProps}>{childNode}</td>;
};
token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')
usrRole = JSON.parse(localStorage.getItem('portalUserDataRole') || '') 
const Expanded = (props:any) => {
  const[dataSet,setDataSet]=useState<any>([])
  const [loader,setloader]=useState<boolean>(false)
  const [openModal, setopenModal] = useState<any>(false)
  const [tag, setTag] = useState<any[]>([])
  const [tagSeverity, setTagSeverity] = useState<any[]>([])
  const [form] = Form.useForm()
  const [productData, setProductData] = useState<any>('')
  const inputRef2 = useRef<any>(null);
  const [name, setName] = useState('');
  useEffect(()=>{
    getSupplierData()

  },[props,productData])
  
  const TagModalFunction = (item: any) => {
    setopenModal(true)
    getTagDetails(item)

  }
 
  const getTagDetails = (data: any) => {
    setProductData(data)
  
   

    fetch(
      `${mainConfig.MAIN_ENDPOINT}/api/get-product-tags-details/${data !== undefined ? data : 850}`,
      {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      }
    )
      .then(function (response) {
        if (response.status != 200) {
          return
        }
        response.json().then(function (data) {

          splitDataStoreTags(data.data)
        
        })
      })
      .catch(function (err) {
        setopenModal(false)
      })
  }
  const splitDataStoreTags = (data: any) => {
    let tempArr2 = new Array(),
      tempArr3 = new Array()
    data.tags.tags.map((i: any) => {
      let obj = {value: i.tag_id, label: i.name}
      tempArr3.push(obj)
    })
    data.tags.severities.map((i: any) => {
      let obj = {value: i.id, label: i.name}
      tempArr2.push(obj)
    })
    setTag(tempArr3)
    setTagSeverity(tempArr2)
  }
  const handleSave = (row:any) => {
    setloader(true)
    if(row.oldPrice===row.price || row.oldPrice==row.price)
    {
     message.error("");
     return
    }
     let payLoad = {
       pricing_id: row.pcid,
       negotiated_price: row.price,
     }
     fetch(`${mainConfig.MAIN_ENDPOINT}/api/update-supplier-pricing`, {
       method: 'post',
       headers: {
         Authorization: `Bearer ${token}`,
         Accept: 'application/json',
         'Content-Type': 'application/json',
       },
       body: JSON.stringify(payLoad),
     })
       .then(function (data) {
         data.json().then(function (data) {
          message.success(data.data)
          getSupplierData()
          setloader(false)
         })
        
       })
       .catch(function (error) {
        
       })
      
   };
  const getSupplierData = () => {
    setloader(true)
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-supplier-pricing/${props.record["ProductAC4"]}`, {
      method: 'get',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then(function (response) {
        if (response.status != 200) {
          setloader(false)
            }
        response.json().then(function (data) {
        
          if (data.success == true) {
            setDataSet(data.data)
            setloader(false)
           
          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
        setloader(false)
      })
  }
  const handleCancel = () => {
    setopenModal(false)
  }
  const removeTag=(tag:any)=>{

 
    let tagObj={
     
      // id:tag.ptagid,
      severity:tag.severity,
      tag_id:tag.tag_id,
    }
   
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/remove-tag/${tag.pc_id}`, {
      method: 'put',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(tagObj),
    })
      .then(function (data) {
     
        data.json().then(function (data) {
          if(!data || data.data.error=="No any product Supplier data found") return
       message.success(data.data)
          // setDataSigmaPricing(data.data.products)
          // setrowCnt(data.data.rowCnt)
          getSupplierData()
    
  
        })
       
      })
      .catch((error) =>{console.log(error)})
      setloader(false)
   }
  const onFinish = (data: any) => {
   setloader(true)
    var tag_id="",custom_tag=""
    if(data.tag && !isNaN(data.tag))
    {
      tag_id=data.tag

    }
    else{
     
      custom_tag=data.tag
    }

    const tagObj = {
      tag_id: `${tag_id}`,
      severity: `${data.severity}`,
      pcid : `${productData.pcid}`,
      custom_tag:`${custom_tag}`,
     
    }

  
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/assign-tag`, {
      method: 'post',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(tagObj),
    })
      .then(function (data) {
        data.json().then(function (data) {
          if (data.success == true) {
            message.success(data.data)
            getSupplierData()
          }
        })
        if (data.status == 204) {
        }
      })
      .catch(function (error) {})
    setopenModal(false)
   setloader(false)

  }

    const columns = [
      {
        title: 'Supplier Code',
        dataIndex: 'supp_code',
        key: 'supp_code',
        className:'table-color-1',
        sorter: (a:any, b:any) => a.supp_code.localeCompare(b.supp_code),
      },
      {
        title: 'Product Code',
        dataIndex: 'product_code',
        key: 'product_code',
        sorter: (a:any, b:any) => a.product_code.localeCompare(b.product_code),
       
      },
     
     
      {
        title: 'Forecast',
        dataIndex: 'forecast',
        key: 'forecast',
        sorter: (a:any, b:any) => a.forecast.localeCompare(b.forecast),
        render:(item:any,records:any)=>{
          if(!item)
          {
            return <>-</>
          }
            return item
          }
      },
      {
        title: 'Price',
        dataIndex: 'price',
        key: 'price',
        // editable: true,
        width:"10%",
        // className:" text-danger",
        render: (item: any) => {
         
         
          if (!item) {
            return <>-</>
          }
          return <> {Helper.addZero(item)}</>
        },
        sorter: (a:any, b:any) => a.price.localeCompare(b.price),
        multiple: 1,

      },
    
      {
        title: 'Date',
        dataIndex: 'date',
        key: 'date',
        render:(item:any)=>{
          if(!item)
          {
            return <>-</>
          }
            return moment(item).format('DD-MMM-YYYY')
          },
         
          sorter:  (a:any, b:any) => a.date.localeCompare(b.date),
          
      },
      {
        title: 'Comments',
        key: 'tags',
        dataIndex: 'tags',
        render: (tags :any) => {
         
         if(!tags || tags.length==0 ) return <>-</>;
         return <>
            {tags.map((tag:any) => {
         
            if(!tag && !tag.name) return;
  
              let color = tag.length > 5 ? 'geekblue' : 'green';
            
              return (
                <Tag color={tag.severity=="1"?"green":tag.severity=="2"?"warning":"error"} closable onClose={(e:any)=>{removeTag(tag)}} key={tag.tag_id} >
                  {tag.name}
                </Tag>
               
              );
            })}
          </>
        }
      },
      
      
      
     
    ];
    const onNameChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      setName(event.target.value);
    };
    const addItem = (e: React.MouseEvent<HTMLButtonElement | HTMLAnchorElement>) => {
      e.preventDefault();
      
 
     tag.push({value:name,label:name})
      setName('');
      setTimeout(() => {
        inputRef2.current?.focus();
      }, 0);
    };
    const components = {
      body: {
        row: EditableRow,
        cell: EditableCell,
      },
    };
    const columns1 = columns.map((col) => {
     
      return {
        ...col,
        onCell: (record:any) => ({
          record,
         
          dataIndex: col.dataIndex,
          title: col.title,
          handleSave: (row:any)=>{
          handleSave({...record,oldPrice: record.price, ...row})
          },
        }),
      };
    });
    
    return <><Table  components={components}  columns={columns1} dataSource={dataSet} pagination={false} rowClassName={() => 'editable-row'}   bordered loading={loader} />
    
    <Modal
        title='Attach Tag'
        open={openModal}
    
        onCancel={handleCancel}
        footer={null}
        destroyOnClose={true}
      >
        <Form
          form={form}
          name='control-hooks'
          onFinish={onFinish}
          style={{
            maxWidth: 600,
          }}
          preserve={false}
          
        >
          <div className="m-4">
          <Form.Item
            name='tag'
            label='Tag'
            rules={[
              {
                required: true,
              },
            ]}
            labelCol={{
              span: 4,
            }}
            wrapperCol={{
              span: 16,
            }}
          >
            
              <Select
      style={{
        width: 300,
      }}
      placeholder="Attach tag"
      dropdownRender={(menu) => (
        <>
          {menu}
          <Divider
            style={{
              margin: '8px 0',
            }}
          />
          <Space
            style={{
              padding: '0 8px 4px',
            }}
          >
            <Input
              placeholder="Please enter new tag"
              ref={inputRef2}
              value={name}
              onChange={onNameChange}
              onKeyDown={(e) => e.stopPropagation()}
            />
            <Button type="text" icon={<PlusOutlined />} 
            onClick={addItem}
            >
              Add Tag
            </Button>
          </Space>
        </>
      )}
      options={tag.map((item) => ({
        label: item.label,
        value: item.value,
      }))}
      allowClear
      showSearch
 
    />
          </Form.Item>
          <Form.Item
            name='severity'
            label='Severity'
            rules={[
              {
                required: false,
              },
            ]}
            labelCol={{
              span: 4,
              
            }}
            wrapperCol={{
              span: 16,
            }}
          >
            <Select options={tagSeverity} showSearch={true} />
          </Form.Item>
          </div>
          <Form.Item>
            <Button type='primary' htmlType='submit'>
              Submit
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </>
    
    
    ;
  };

export default Expanded


